.temp{
    background-color: #0a192f;
    color: #64ffda;
}
.navigation{
    color: #8892b0;
    font-size: 16px;
}
.navigation:hover,.highlights{
    color: #64ffda;
    cursor: pointer;
}
.heading{
    color: #ccd6f6;
    font-size: 30px;
    font-weight: bold;
}
.active{
    color: #64ffda;
}
.icons{
    width:50px;
    height: 50px;
    margin: auto;
    cursor: pointer;
}
.menu{
    width: 40px;
    height: 40px;
    cursor: pointer;
}
.social{
    width: 30px;
    height: 30px;
}
.social:hover{
    color: #64ffda;
    margin-left: 20px;
}
@media all and (min-width:200px) and (max-width:479px){
.heading{
    font-size: 20px;
}
.links{
    font-size: 14px;
}
.icons{
    width:30px;
    height: 30px;
}
.menu{
    width: 25px;
    height: 25px;
}
}
